export class Contrato {    
    _id: String;
    texto: String;
    dataCriacao: Date;

    /*
        1- Criado ou Alterado pelo fornecedor
        2- Aceito pelo consumidor.
        3- Contrato no aceito pelo consumidor.
        4- Solicitao de excluso realizada pelo fornecedor.
        5- Solicitao de excluso realizada pelo consumidor.
      
    */
    status: Number;
    valor: Number;
}



